@import url(https://fonts.googleapis.com/css?family=Roboto:400,700,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.contries{padding:40px 5px}.col-crountry-container{min-height:220px;margin:10px auto}.card-country{box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);transition:0.3s;height:100%;background-color:#fff}.card-country img{width:100%}.card-country:hover{box-shadow:0 8px 16px 0 rgba(0,0,0,0.2)}.card-body{color:#152836}

.navbar{background-color:#326081 !important}

.container-page-not_found{width:90%;margin:20px auto;padding:20px}.container-page-not_found img{display:block;margin-left:auto;margin-right:auto;width:50%}

.contry{padding:40px 5px}.view-country-flag img{width:100%;border-radius:6px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);transition:0.3s}.country-info-card{background-color:#fff;margin-bottom:20px;padding:20px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);transition:0.3s;border-radius:6px;color:#326081}.header-country-name{margin-bottom:10px;color:#326081}.header-country-name h2{text-align:center;font-size:4vw;font-family:"Comic Sans MS", cursive, sans-serif}.country-basic-info-text{margin-top:20px}.country-basic-info-text span{font-weight:lighter}.table-header-view-country{color:#326081}

html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{margin:0;padding:0;line-height:1;font-family:"Roboto", sans-serif;color:#202020;background-color:#e6e6e6;font-smooth:always;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.loadingIcon{display:block;margin-left:auto;margin-right:auto}

.calling-code-container{padding:40px 5px}.calling-search-from-container{display:block;margin:auto;width:70%}.row-search-results{margin-top:30px}.calling-code-card{background-color:#fff;margin-bottom:20px;padding:20px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);transition:0.3s;border-radius:6px;color:#326081}.calling-code-card h2{text-align:center;font-family:"Comic Sans MS", cursive, sans-serif;margin-bottom:20px}.country-basic-info-text{margin-top:20px}.country-basic-info-text span{font-weight:lighter}

.calling-code-container{padding:40px 5px}.calling-search-from-container{display:block;margin:auto;width:70%}.row-search-results{margin:30px 10px}.calling-code-card{background-color:#fff;margin-bottom:20px;padding:20px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);transition:0.3s;border-radius:6px;color:#326081}.calling-code-card h2{text-align:center;font-family:"Comic Sans MS", cursive, sans-serif;margin-bottom:20px}.search-button{background-color:#326081;color:#fff}.search-button:hover{color:#fff}

